body {
  /* font-family: Arial, sans-serif; */
  font-size: 16px;
  padding: 0;
  margin: 1em 1em 4em;
  line-height: 20px;
}

header {
  background-color: #204c72;
  color: white;
  padding: 1em;
  margin: -1em -1em 1em -1em;
  display: flex;
  align-items: center;
}

header a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  margin-right: 1em;
}

header .logout {
  margin-left: auto;
}

